@charset "UTF-8";



/* --------------------------------
    blog-contents
-------------------------------- */

.blog-contents {
    // margin-left: 40px;
    // margin-right: 40px;
    padding-top: 80px;
    padding-bottom: 40px;

    @include media-mobile {
        // margin-left: 0;
        // margin-right: 0;
        padding-top: 60px;
        padding-bottom: 20px;
    }

    #moguobject {
        width: 200px;
        height: 200px;
        margin: -30px auto 0;
        transition: all .25s $ease .5s;
        opacity: 0;

        @include media-mobile {
            margin-bottom: 8px;
        }
    }
}

.is-loaded .blog-contents #moguobject {
    opacity: 1;
}



/* --------------------------------
    blog-article
-------------------------------- */

.page-index {

    .blog-contents {
        background: #e8edf3;
        font-size: 0;
        padding-bottom: 100px;

        @include media-mobile {
            padding-bottom: 50px;
        }
    }

    .blog-article {

        > article {
            position: relative;
            width: calc(100% / 3);
            display: inline-block;
            vertical-align: top;
            padding: 20px;

            @include media-mobile {
                width: 100%;
                padding: 0 20px 20px;
            }
        }

        a {
            display: block;
            background: #fff;
            padding: 40px;
            transition: all .25s $ease;
            position: relative;

            &:hover {
                box-shadow: 0 0 8px rgba(0,0,0,.12);
            }
        }

        .meta {
            text-align: center;

            h2 {
                font-size: 18px;
                line-height: 1.5;
                font-weight: normal;
                font-style: italic;

                span {
                    color: #000;
                    background: #ede9ce;
                }
            }

            .category {
                display: inline-block;
                color: #000;
                font-size: 14px;
                margin-bottom: 2em;
                letter-spacing: .15em;
                padding-left: .15em;
                font-weight: bold;
                position: relative;

                &:before,
                &:after {
                    content: " ";
                    display: block;
                    width: 40px;
                    height: 2px;
                    background: #eee;
                    position: absolute;
                    top: 50%;
                    margin-top: -1px;
                }
                &:before {
                    left: -50px;
                }
                &:after {
                    right: -50px;
                }
            }

            time {
                display: inline-block;
                margin-top: 2em;
                font-size: 14px;
                color: #525252;
            }
        }
    }

}



/* --------------------------------
    search
-------------------------------- */

.search-result {
    font-size: 18px;
    text-align: center;
    letter-spacing: .15em;
    margin-top: 20px;
    margin-bottom: 42px;
    line-height: 1.5;

    @include media-mobile {
        font-size: 14px;
        padding-left: 40px;
        padding-right: 40px;
        margin-top: 0;
        margin-bottom: 38px;
    }

    span {
        background: #ede9ce;
        margin-right: 6px;
    }
}

.search-error {
    font-size: 24px;
    text-align: center;
    letter-spacing: .5em;
    margin-top: 1em;

    @include media-mobile {
        font-size: 18px;
        margin-top: -1em;
        letter-spacing: .35em;
    }
}



/* --------------------------------
    404
-------------------------------- */

.error-404 {

    h2 {
        font-size: 404px;
        text-align: center;
        margin-bottom: 40px;

        @include media-mobile {
            font-size: 150px;
        }
    }

    .btn-return {
        text-align: center;

        a {
            display: inline-block;
            color: #000;
            text-transform: uppercase;
            letter-spacing: .15em;
            font-weight: bold;
            padding-left: .15em;
            position: relative;
            padding-bottom: 6px;

            &:before {
                content: " ";
                display: block;
                position: absolute;
                bottom: 0;
                left: 0;
                background: #000;
                height: 2px;
                width: 100%;
                transition: all .25s $ease;
            }

            &:hover {
                &:before {
                    width: 0;
                }
            }
        }
    }
}







