@charset "UTF-8";

/* --------------------------------
    blog-article (single)
-------------------------------- */

.page-single {

    .blog-article {
        margin-bottom: 120px;

        @include media-mobile {
            margin-bottom: 100px;
        }

        > article {
            background: #fff;

            > figure {
                position: relative;
                height: 600px;
                overflow: hidden;
                background: #e8edf3;

                @include media-mobile {
                    height: 300px;
                }

                span {
                    display: block;
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    background-repeat: no-repeat;
                    background-position: center center;
                    background-size: cover;
                }
            }
        }

        .meta {
            text-align: center;
            padding-bottom: 80px;

            @include media-mobile {
                padding-left: 20px;
                padding-right: 20px;
                padding-bottom: 60px;
            }

            h2 {
                font-size: 32px;
                line-height: 1.5;
                letter-spacing: .05em;
                font-weight: normal;
                font-style: italic;

                @include media-mobile {
                    font-size: 20px;
                }

                span {
                    color: #000;
                    background: #ede9ce;
                }
            }

            .category {
                display: inline-block;
                color: #000;
                font-size: 16px;
                margin-bottom: 2em;
                letter-spacing: .15em;
                padding-left: .15em;
                font-weight: bold;
                position: relative;
                text-transform: capitalize;

                &:before,
                &:after {
                    content: " ";
                    display: block;
                    width: 40px;
                    height: 2px;
                    background: #eee;
                    position: absolute;
                    top: 50%;
                    margin-top: -1px;
                }
                &:before {
                    left: -50px;
                }
                &:after {
                    right: -50px;
                }
            }

            > .wrap {
                @include clearfix;
                display: inline-block;
                vertical-align: top;
                margin-top: 2em;

                time {
                    float: left;
                    font-size: 14px;
                    color: #525252;
                }

                .author {
                    @include clearfix;
                    float: left;
                    font-size: 14px;
                    color: #525252;
                    letter-spacing: .05em;
                    margin-left: 30px;

                    @include media-mobile {
                        width: 120px;
                        margin-left: 20px;
                    }

                    > i {
                        float: left;
                        width: 24px;
                        height: 24px;
                        background: url("../../icon.png") no-repeat center center / cover;
                        border: 1px solid #eee;
                        border-radius: 50%;
                        overflow: hidden;
                        margin-top: -5px;
                        margin-right: 8px;

                        @include media-mobile {
                            margin-right: 0;
                        }
                    }
                }
            }
        }

        .letter-body {
            margin-top: 100px;
            color: #525252;
            padding: 0 40px;
            font-family: Helvetica, "游ゴシック", "Yu Gothic", sans-serif;

            @include media-mobile {
                margin-top: 60px;
                padding: 0 20px;
            }

            > p {
                font-size: 18px;
                line-height: 2.2;
                margin-bottom: 40px;

                @include media-mobile {
                    font-size: 16px;
                    line-height: 2;
                    margin-bottom: 30px;

                    br {
                        display: none;
                    }
                }

                &:last-of-type {
                    margin-bottom: 0;
                }

                > code {
                    background: #eee;
                    padding: 0 5px;
                    margin: 0 5px;
                    border-radius: 4px;
                }

                > img {
                    max-width: 100%;
                    height: auto;
                    border: 1px solid #eee;
                    padding: 10px;
                }

                a {
                    display: inline-block;
                    padding-left: 5px;
                    padding-right: 5px;
                    text-decoration: underline;
                    color: #929292;

                    &:hover {
                        text-decoration: none;
                    }
                }
            }

            > h1,
            > h2,
            > h3 {
                font-size: 26px;
                font-weight: normal;
                border-bottom: 2px solid #eee;
                margin-bottom: 30px;
                padding-top: 40px;
                padding-bottom: 10px;
                letter-spacing: .025em;
                line-height: 1.6;

                @include media-mobile {
                    font-size: 20px;
                    font-weight: bold;
                    margin-bottom: 18px;
                    padding-top: 30px;
                    padding-bottom: 6px;
                }
            }

            > h4,
            > h5,
            > h6 {
                font-size: 20px;
                font-weight: bold;
                margin-bottom: 16px;
                padding-top: 20px;
                padding-bottom: 4px;
                letter-spacing: .05em;
                line-height: 1.8;

                @include media-mobile {
                    font-size: 16px;
                    margin-bottom: 8px;
                }
            }

            > pre {
                margin-bottom: 40px;

                @include media-mobile {
                    margin-bottom: 30px;
                }
            }
        }

    }

    .pager {
        @include clearfix;
        list-style: none;
        padding-top: 100px;

        @include media-mobile {
            padding-top: 60px;
        }

        li {
            @include clearfix;
            width: 50%;

            &.prev {
                float: left;
            }
            &.next {
                float: right;
            }

            @include media-mobile {
                float: none;
                width: 100%;
                margin-bottom: 40px;
                &:last-child {
                    margin-bottom: 0;
                }
            }

            a {
                display: block;
                padding: 0 40px;

                &:hover {
                    figure {
                        span {
                            opacity: .5;
                            transform: scale(1.05);
                        }
                    }
                }

                @include media-mobile {
                    padding: 0 20px;
                }
            }

            figure {
                float: left;
                width: 100%;
                height: 250px;
                position: relative;
                background: #e8edf3;
                overflow: hidden;

                @include media-mobile {
                    height: 150px;
                }

                span {
                    display: block;
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    background-repeat: no-repeat;
                    background-position: center center;
                    background-size: cover;
                    transition: all .25s $ease;
                }
            }

            h3 {
                float: left;
                font-weight: normal;
                font-size: 20px;
                line-height: 1.6;
                font-style: italic;
                padding: 1em .5em 0;

                @include media-mobile {
                    margin-top: 10px;
                    font-size: 18px;
                    padding: 0;
                }

                span {
                    color: #000;
                    background: #ede9ce;
                }
            }
        }
    }

    .btn-return {
        margin-top: 96px;
        padding-top: 100px;
        border-top: 1px solid #eee;
        text-align: center;

        @include media-mobile {
            margin-top: 60px;
            padding-top: 60px;
        }

        a {
            display: inline-block;
            color: #000;
            text-transform: uppercase;
            letter-spacing: .15em;
            font-weight: bold;
            padding-left: .15em;
            position: relative;
            padding-bottom: 6px;

            &:before {
                content: " ";
                display: block;
                position: absolute;
                bottom: 0;
                left: 0;
                background: #000;
                height: 2px;
                width: 100%;
                transition: all .25s $ease;
            }

            &:hover {
                &:before {
                    width: 0;
                }
            }
        }
    }

}